.container {
    width: 100%;
    height: 100vh;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    .traffic {
        display: flex;
        flex-direction: column;
        align-items: center;
        .soporte {
            background: darkgray;
            height: 100px;
            width: 15px; }
        .base {
            background: black;
            border-radius: 10px;
            padding: 15px; } } }

.light {
    height: 60px;
    width: 60px;
    border-radius: 50%;
    margin: 15px 0px;
    transition: all 0.3s ease-in-out; }
.light.yellow {
    background: yellow; }
.light.red {
    background: red; }
.light.green {
    background: green; }
.light.red.active {
    box-shadow: 0px 0px 20px 7px red; }
.light.yellow.active {
    box-shadow: 0px 0px 20px 7px yellow; }
.light.green.active {
    box-shadow: 0px 0px 20px 7px green; }

button {
    margin: 90px;
    background: lightgray;
    padding: 20px;
    color: black;
    border-radius: 4px;
    box-shadow: none;
    border: 1px gray solid;
    cursor: pointer;
    &:hover {
        background: black;
        color: white; } }
